import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue';
import {
	cityData
} from '@/common/city.data-3';
import {
	uploadImg
} from "@/common/util.js"
export default {
	components: {
		mpvuePicker,
	},
	data() {
		return {
			ctx: this.$ctx,
			mode: 'selector',
			deepLength: 3, // 几级联动
			pickerValueDefault: [0, 0, 0], // 初始化值
			pickerValueValue: ["210000", "210400", "210411"],
			pickerValueArray: cityData, // picker 数组值
			pickerCity: '请选择所在城市',
			themeColor: '', // 颜色主题
			pickerArray: cityData,
			industry: [],
			ids: [],
			arr: [],
			form: { 
			},
			//声明
			showTextarea:true,
			hideTextarea:false,
			posterImg: [],
		}
	},
	onLoad(e) {
		this.$getJson('/api/v2/vue/sqPlus/company/companyInfo.jsp', {
			data: JSON.stringify({
				companyId: e.companyId||202
			})
		}, 'POST', res => {
			console.log('企业信息==>', res.data.cjCompany);
			console.log('企业信息==>2', res);
			this.form = res.data.cjCompany;
			if(this.form.posterImgArr == null){
				this.posterImg = []
			}else{
				this.form.posterImgArr.forEach((img, index) => {
					if (img != "") {
						this.posterImg.push(img)
					}
				})
			}
			if(this.form.name == "企业名称待编辑"){
				this.form.name = ""
			}
			this.form['companyId'] = this.form['id'];
			this.form['job'] = res.data.job;
			if (res.data.industryIds != '') {
				this.ids = res.data.industryIds.split(',').map(Number);
			}
			delete this.form.cjUser
			delete this.form['id']
			this.getIndex()
		})
	},
	mounted() {








	},
	methods: {
		nativeDel(data, idx) {
			console.log(data.splice(idx, 1));
		},
		posterImgClk() {
			const _this = this;
			this.$upload(1, res => {
				console.log('回调=>', res)
				console.log('回调=>', _this.posterImg)
				let index = this.posterImg.length;
				this.$set(this.posterImg, index, res);
			})
		},
		// 省市区根据value 获取 名称和索引
		getIndex() {
			// pickerValueValue
			let defalutIndex = [];
			let defalutName = ''
			cityData.forEach((item, index) => {
				// provinceId(省id):
				// cityId(市ID): 
				// countyId(县区ID): 
				if (item.value == this.form.provinceId) {
					console.log('进来了吗')
					defalutIndex.push(index)
					defalutName = item.label;
					console.log(item)
					item.children.forEach((element, i) => {
						if (element.value === this.form.cityId) {
							defalutIndex.push(i)
							defalutName += '-' + element.label;
						}
						element.children.forEach((item1, index1) => {
							if (item1.value === this.form.countyId) {
								defalutIndex.push(index1)
								defalutName += '-' + item1.label;
								this.pickerValueDefault = defalutIndex
								console.log('默认值', this.pickerValueDefault)
								this.pickerCity = defalutName
								console.log('默认值', defalutName)
							}
						})
					})
				}
			})
			this.$getJson(`/api/v2/vue/common/do/industrys.jsp`, {
				data: JSON.stringify({
					level: 1
				})
			}, 'POST', res => {
				console.log('选项=>', res.data)
				res.data.forEach((item, index) => {
					let data = {
						id: item.id,
						name: item.name,
						isSel: 0,
					}
					this.ids.forEach((id, index) => {
						if (id == item.id) {
							data.isSel = 1
						}
					})
					this.industry.push(data)
				})
				console.log("industry-----", this.industry)
			})
		},
		myUploadImgUrl(rsp) {
			this.form.logoPath = rsp.data;
		},
		myUploadImgUrl1(rsp) {
			this.form.shareLogoPath = rsp.data;
		},
		setImg() {




			const _this = this;
			this.$upload(1, res => {
				console.log('url===>', res)
				_this.form.logoPath = res;
			})

		},
		setShareImg() {




			const _this = this;
			this.$upload(1, res => {
				console.log('url===>', res)
				_this.form.shareLogoPath = res;
			})

		},
		submit(e) {
			this.form['industryIds'] = this.ids.join(',');
			console.log('表单', this.form);
			this.form.posterImg = this.posterImg;
			this.$getJson('/api/v2/vue/sqPlus/company/editCompanyInfo.jsp', {
				data: JSON.stringify(
					this.form
				)
			}, 'POST', res => {
				uni.showToast({
					title: "保存成功",
					duration: 1000,
					success() {
						setTimeout(function() {
							//要延时执行的代码
							uni.navigateBack({
								delta: 1
							})
						}, 1000)
					}
				})
			})
		},
		// 地址选择
		showPicker() {
			// this.$refs.mpvuePicker._getPickerLabelAndValue(["210000", "210400", "210411"],'selector').then(res => {
			// 	console.log('=================>',res)
			// })
			this.pickerValueArray = this.pickerArray;
			this.mode = 'multiLinkageSelector';
			this.deepLength = 3;
			// this.pickerValueDefault = [0, 0, 0];
			this.themeColor = '#004da0';
			this.$refs.mpvuePicker.show();
			
			this.showTextarea = !this.showTextarea;
			this.hideTextarea = !this.hideTextarea;
		},
		showPickerView() {
			this.$refs.mpvuePicker.show();
		},
		onCancel(e){
			//确认后恢复原样
			this.showTextarea = !this.showTextarea;
			this.hideTextarea = !this.hideTextarea;
		},
		onConfirm(e) {
			if (this.mode === 'selector') {
				this.pickerText = e.label;
			} else if (
				this.mode === 'multiLinkageSelector' &&
				this.deepLength === 2
			) {
				this.pickerCity = e.label;
			} else if (
				this.mode === 'multiLinkageSelector' &&
				this.deepLength === 3
			) {
				this.pickerCity = e.label;
				console.log(e)
				this.form.provinceId = e.value[0];
				this.form.cityId = e.value[1];
				this.form.countyId = e.value[2];
			}
			
			this.showTextarea = !this.showTextarea;
			this.hideTextarea = !this.hideTextarea;
		},
		onChange(e) {
			console.log(e);
		},
		addReturn() {
			uni.navigateBack({

			})
		},
		//选择所属行业
		checkedBox(item, i) {
			if (this.ids.indexOf(item.id) === -1) {
				this.ids.push(item.id)
				this.industry.forEach((a, index1) => {
					if (a.id == item.id) {
						this.industry[index1].isSel = 1
					}
				})
			} else {
				let index = this.ids.indexOf(item.id)
				this.ids.splice(index, 1)
				this.industry.forEach((a, index1) => {
					if (a.id == item.id) {
						this.industry[index1].isSel = 0
					}
				})
			}
			console.log('ids===>', this.ids)
			if (this.arr.includes(i)) {
				//includes()方法判断是否包含某一元素,返回true或false表示是否包含元素，对NaN一样有效
				//filter()方法用于把Array的某些元素过滤掉，filter()把传入的函数依次作用于每个元素，然后根据返回值是true还是false决定保留还是丢弃该元素：生成新的数组
				this.arr = this.arr.filter(function(ele) {
					return ele != i;
				});
				//this.arr=this.arr.filter((ele)=>ele!=i);
				//filter()为假时删除
			} else {
				this.arr.push(i);
			}
		}
	}
};
